$blue:#0055b8;
$gray:#ccd4de;

@mixin banner-texts {
  margin: 8px 16px 8px 0;
  padding: 15px 16px 17px;
  font-size: 18px;
  border-radius: 4px;
}

@mixin fs16-ln24-center {
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

@mixin flex-sb {
  display: flex;
  justify-content: space-between;
}

@mixin col {
  display: flex;
  flex-direction: column;
}

.banner {
  display: flex;
  margin-top: 63px;
  padding: 0 16px;
}

.banner-texts {
  @include col;
  align-items: flex-start;
  animation: 1.5s ease-in-out slideOffer1;
}

.banner-image {
  animation: 1.5s ease-in-out slideOffer3;
}

.banner-texts__header {
  font-size: 62px;
  line-height: 1.125;
  font-weight: 600;
  margin-bottom: .35em;
}

.banner-texts__subheader {
  font-size: 20px;
  line-height: 1.6;
  margin-bottom: 24px;
  font-weight: 400;
}

.banner-texts__calculator {
  box-shadow: 0 0 0 1px $blue;
  background-color: $blue;
  color: white;
  @include banner-texts;
  font-weight: 500;
  line-height: 24px;
}

.banner-texts__calculator:hover {
  background-color: #1c65b9;
  transition: background-color 0.5s;
}

.banner-texts__about {
  box-shadow: 0 0 0 1px $gray;
  font-weight: 400;
  min-width: 176px;
  @include banner-texts;
  text-align: center;
}

/*offer*/
.offer__img {
  width: 96px;
  margin-bottom: 24px;
}

.offers__header {
  font-size: 40px;
  line-height: 1.3;
  font-weight: 600;
  flex-basis: 100%;
  flex-shrink: 0;
  text-align: center;
  margin-bottom: 50px;
}

.offer-sliding-left {
  animation: 1.5s ease-in-out slideOffer1;
}

.offer-sliding-right {
  animation: 1.5s ease-in-out slideOffer3;
}

.offer__header {
  font-size: 24px;
  line-height: 1.33;
  text-align: center;
  font-weight: 500;
  min-height: 62px;
}

.offer__link {
  @include fs16-ln24-center;
  color: $blue;
  font-weight: 500;
  display: block;
}

.offer__content {
  font-weight: 400;
  margin-bottom: 10px;
  @include fs16-ln24-center;
  min-height: 144px;
}

.offers {
  margin-top: 111px;
  flex-wrap: wrap;
  @include flex-sb;
  padding: 0 16px;
  margin-bottom: 80px;
}

.offer {
  width: 33%;
  @include col;
  align-items: center;
  max-width: 320px;
}

/*testimonials*/
.testimonial {
  @include col;
  align-items: center;
  padding-left: 16px;
}

.sliding {
  animation: 1s ease-in-out slide;
}

.resize {
  animation: 1s ease-in-out resize;
}

.testimonial__img {
  border-radius: 50%;
  width: 96px;
}

.testimonial__content {
  font-size: 24px;
  line-height: 40px;
  display: block;
  text-align: center;
  margin: 15px 0 20px;
}

.testimonial__company {
  font-size: 16px;
  color: #768190;
}

.testimonials__list {
  width: 100px;
  @include flex-sb;
  margin-top: 14px;
}

.testimonials__list__button {
  width: 16px;
  border-radius: 50%;
  height: 16px;
  cursor: pointer;
  background-color: $gray;

  &--active {
    @extend .testimonials__list__button;
    background-color: $blue;
  }
}

.overall__header {
  font-size: 40px;
  line-height: 1.3;
  font-weight: 600;
}

.overall__content {
  font-size: 18px;
  line-height: 1.6;
  font-weight: 400;
  margin-bottom: 1.2em;
  padding-right: 16px;
}

.overall__link {
  color: #00479a;
  font-size: 18px;
  font-weight: 500;
}

.testimonials {
  display: flex;
  background-color: #F2F5F7;
  position: relative;
  /* for child pseudo-element */
  margin: 0 -600rem;
  /* add back negative margin value */
  padding: 80px 600rem;
}

.testimonials__main {
  width: 55%;
  flex-shrink: 0;
  padding-right: 32px;
  @include col;
  align-items: center;
}

/* culture */
.culture {
  display: grid;
  grid-template-columns: 40% 60%;
  margin: 80px 0;
  padding: 0 16px;
}

.culture .overall {
  padding-right: 32px;
}

.culture__img {
  width: 100%;
}

@media (max-width: 1000px) {
  .banner-texts {
    @include col;
    animation: none;
  }

  .banner-image {
    animation: none;
  }

  .offer-sliding-left-mobile {
    animation: 1.5s ease-in-out slideOffer1;
  }

  .offer-sliding-right-mobile {
    animation: 1.5s ease-in-out slideOffer3;
  }

  .banner-texts__header {
    text-align: center;
  }

  .banner-texts {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .banner-texts__subheader {
    text-align: center;
  }

  .banner-texts__calculator {
    min-width: 176px;
    text-align: center;
    animation: 1.5s ease-in-out slideOffer1;

  }

  .culture {
    grid-template-columns: 50% 50%;
  }

  .estimation-main,
  .solutions {
    width: 920px;
    padding: 20px 20px 12px;
  }

  .from__language,
  .to__language {
    width: 380px;
  }

  .banner {
    flex-direction: column-reverse;
  }

  .banner-image {
    height: 100%;
    width: 100%;
  }

  .banner-texts__header {
    font-size: 36px;
  }

  .banner-texts__calculator,
  .banner-texts__about {
    margin: 8px 16px 8px 0;
    padding: 8px;
    font-size: 14px;
  }

  .banner-texts__about {
    animation: 1.5s ease-in-out slideOffer3;
  }

  .offers__header {
    font-size: 32px;
  }

  .offers {
    margin-top: 60px;
    margin-bottom: 40px;
    flex-direction: column;
    align-items: center;
  }

  .offer {
    width: 100%;
    margin-bottom: 20px;
  }


  .offer__header {
    min-height: 0;
  }

  .offers__header {
    margin-bottom: 24px;
  }

  .testimonials {
    flex-direction: column;
    align-items: center;
  }

  .overall__header,
  .overall__content,
  .overall__link {
    text-align: center;
  }

  .overall {
    display: flex;
    flex-direction: column;
  }

  .testimonial__content {
    font-size: 14px;
    line-height: 1.2;
  }

  .testimonial {
    padding-left: 0;
  }

  .testimonials__main {
    width: 65%;
    padding-right: 0;
  }

  .overall__header {
    font-size: 32px;
  }

  .overall__content {
    font-size: 14px;
    line-height: 1.4;
    padding-left: 16px;

  }

  .culture {
    display: flex;
    margin: 40px 0;
    flex-direction: column;
  }
}

@keyframes resize {
  0% {
    transform: scale(0.01);
  }

  80% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }

}

@keyframes slide {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideOffer1 {
  0% {
    transform: translateX(-100%) rotate(-20deg);
  }

  80% {
    transform: translateX(10%) rotate(5deg);
  }

  100% {
    transform: translateX(0) rotate(0);
  }
}

@keyframes slideOffer3 {
  0% {
    transform: translateX(100%) rotate(20deg);
  }

  80% {
    transform: translateX(-10%) rotate(-5deg);
  }

  100% {
    transform: translateX(0) rotate(0deg);
  }
}